		&:hover {
			background: #2C4267;
			border-radius: 4px;
			color: white;
			margin-left: 3px;
			margin-right: 3px; } } }
.acors-container label {
	cursor: pointer;
	margin-bottom: 10px;
	display: flex;
	align-items: center;
	height: 50px;
	padding: 13px;
	width: 100%;
	color: #2C4267;
	font-weight: 300;
	box-sizing: border-box;
	z-index: 100;
	font-weight: 900;
	font-size: 18px;
	line-height: 24px {
    margin: 0 0 5px;
    transition: color .35s; }
	position: relative;
	border: 1px solid #E4E4E4;
	border-radius: 4px;
	svg {
		margin-right: 11px; } }
.acors-container input {
    display: none; }
.acors-container label:before {
	content: '\276F';
	position: absolute;
	right: 22px;
	transform: rotate(90deg); }
.acors-container input:checked + label:before {
    transition: transform .35s;
    transform: rotate(270deg); }
.acors-container input:checked + label + .acors-body {
	height: auto;
	margin-top: -9px;
	color: #000; }
.acors-container input:checked + label {
	border-top: 1px solid #E4E4E4;
	border-left: 1px solid #E4E4E4;
	border-right: 1px solid #E4E4E4;
	border-bottom: 0px solid #E4E4E4;
	border-radius: 4px 4px 0px 0px; }
.acors-container input:checked + label + .acors-body {
	border-left: 1px solid #E4E4E4;
	border-right: 1px solid #E4E4E4;
	border-bottom: 1px solid #E4E4E4;
	margin-bottom: 10px; }
